.circle {
    width: 35px;
    height: 35px;
    border-radius: 100%;
    background: #2196f3;
    border: 1px solid #2196f3;
    display: -ms-flexbox;
    display: flex;
    font-size: .8em;
    color: white;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

.circle:hover {
    font-weight: bold;
}


